'use client';

import Image from "next/image";
import { BellIcon, ClockIcon, MessageSquareIcon, UserCheckIcon, XIcon } from "lucide-react";
import { capitialize } from "@/lib/utils";
import { getLanguageFlag } from "@/components/ui/FriendCard";
import NoNotificationFound from "@/components/ui/NoNotificationFound";
import { useNotifications } from "@/hooks/useNotification";

export default function NotificationsPage() {
	// ‚úÖ Hook t·ª± ƒë·ªông fetch data, kh√¥ng c·∫ßn useEffect
	const {
		friendRequests,
		isLoading,
		acceptingRequest,
		rejectingRequest,
		handleAcceptRequest,
		handleRejectRequest
	} = useNotifications();

	const { incomingRequests, acceptedRequests } = friendRequests;

	console.log('üîç Component render:', {
		isLoading,
		incomingCount: incomingRequests.length,
		acceptedCount: acceptedRequests.length
	});

	return (
		<div className="p-4 sm:p-6 lg:p-8">
			<div className="container mx-auto max-w-4xl space-y-8">
				<h1 className="text-2xl sm:text-3xl font-bold tracking-tight mb-6">Notifications</h1>

				{isLoading ? (
					<div className="flex justify-center py-12">
						<span className="loading loading-spinner loading-lg"></span>
					</div>
				) : (
					<>
						{/* INCOMING FRIEND REQUESTS */}
						{incomingRequests.length > 0 && (
							<section className="space-y-4">
								<h2 className="text-xl font-semibold flex items-center gap-2">
									<UserCheckIcon className="h-5 w-5 text-primary" />
									Friend Requests
									<span className="badge badge-primary ml-2">{incomingRequests.length}</span>
								</h2>

								<div className="space-y-3">
									{incomingRequests.map((request) => (
										<div
											key={request._id}
											className="card bg-base-200 shadow-sm hover:shadow-md transition-shadow"
										>
											<div className="card-body p-4">
												<div className="flex items-center justify-between">
													<div className="flex items-center gap-3">
														<div className="avatar w-14 h-14 rounded-full relative overflow-hidden">
															<Image
																src={request.sender.profilePic}
																alt={request.sender.fullName}
																fill
																className="object-cover"
																sizes="56px"
															/>
														</div>
														<div>
															<h3 className="font-semibold">{request.sender.fullName}</h3>
															<div className="flex flex-wrap gap-1.5 mt-1">
																<span className="badge badge-secondary badge-sm">
																	{getLanguageFlag(request.sender.nativeLanguage)}
																	Native: {capitialize(request.sender.nativeLanguage)}
																</span>
																<span className="badge badge-outline badge-sm">
																	{getLanguageFlag(request.sender.learningLanguage)}
																	Learning: {capitialize(request.sender.learningLanguage)}
																</span>
															</div>
														</div>
													</div>

													<div className="flex gap-2">
														<button
															className="btn btn-primary btn-sm"
															onClick={() => handleAcceptRequest(request._id)}
															disabled={acceptingRequest === request._id || rejectingRequest === request._id}
														>
															{acceptingRequest === request._id ? (
																<>
																	<span className="loading loading-spinner loading-xs mr-1" />
																	Accepting...
																</>
															) : (
																'Accept'
															)}
														</button>

														<button
															className="btn btn-ghost btn-sm"
															onClick={() => handleRejectRequest(request._id)}
															disabled={acceptingRequest === request._id || rejectingRequest === request._id}
														>
															{rejectingRequest === request._id ? (
																<>
																	<span className="loading loading-spinner loading-xs mr-1" />
																	Rejecting...
																</>
															) : (
																<>
																	<XIcon className="size-4" />
																	Reject
																</>
															)}
														</button>
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</section>
						)}

						{/* ACCEPTED REQUESTS NOTIFICATIONS */}
						{acceptedRequests.length > 0 && (
							<section className="space-y-4">
								<h2 className="text-xl font-semibold flex items-center gap-2">
									<BellIcon className="h-5 w-5 text-success" />
									New Connections
								</h2>

								<div className="space-y-3">
									{acceptedRequests.map((notification) => (
										<div key={notification._id} className="card bg-base-200 shadow-sm">
											<div className="card-body p-4">
												<div className="flex items-start gap-3">
													<div className="avatar mt-1 size-10 rounded-full relative overflow-hidden">
														<Image
															src={notification.recipient.profilePic}
															alt={notification.recipient.fullName}
															fill
															className="object-cover"
															sizes="40px"
														/>
													</div>
													<div className="flex-1">
														<h3 className="font-semibold">{notification.recipient.fullName}</h3>
														<p className="text-sm my-1">
															{notification.recipient.fullName} accepted your friend request
														</p>
														<p className="text-xs flex items-center opacity-70">
															<ClockIcon className="h-3 w-3 mr-1" />
															Recently
														</p>
													</div>
													<div className="badge badge-success">
														<MessageSquareIcon className="h-3 w-3 mr-1" />
														New Friend
													</div>
												</div>
											</div>
										</div>
									))}
								</div>
							</section>
						)}

						{/* NO NOTIFICATIONS */}
						{incomingRequests.length === 0 && acceptedRequests.length === 0 && (
							<NoNotificationFound />
						)}
					</>
				)}
			</div>
		</div>
	);
}